<template>
	<view class="container">
		<tui-tabs :tabs="tabs" :isFixed="scrollTop>=0" :currentTab="currentTab" selectedColor="#E41F19" sliderBgColor="#E41F19"
		 @change="change"></tui-tabs>
		<!--选项卡逻辑自己实现即可，此处未做处理-->
		<view :class="{'tui-order-list':scrollTop>=0}">
			<view class="tui-order-item" v-for="(order,order_index) in order_list" :key="order.id">
				<tui-list-cell :hover="false" :lineLeft="false">
					<view class="tui-goods-title">
						<view>订单号：{{order.order_id}}</view>
						<view class="tui-order-status">{{order._status._title}}</view>
					</view>
				</tui-list-cell>
				<block v-for="(item,index) in order.cartInfo" :key="item.id">
					<tui-list-cell padding="0" @click="detail(order.order_id)">
						<view class="tui-goods-item">
							<image :src="((item.product_attr_unique=='')?item.productInfo.image:item.productInfo.attrInfo.image)" class="tui-goods-img"></image>
							<view class="tui-goods-center">
								<view class="tui-goods-name">{{item.productInfo.store_name}}</view>
								<view class="tui-goods-attr">{{item.productInfo.attrInfo.suk}}</view>
							</view>
							<view class="tui-price-right">
								<view>￥{{item.truePrice}}</view>
								<view>x{{item.cart_num}}</view>
							</view>
						</view>
					</tui-list-cell>
				</block>
				<tui-list-cell :hover="false" :last="true">
					<view class="tui-goods-price">
						<view>共{{order.total_num}}件商品 合计：</view>
						<view class="tui-size-24">￥</view>
						<view class="tui-price-large">{{order.total_price}}</view>
					</view>
				</tui-list-cell>
				<view class="tui-order-btn">
					<view class="tui-btn-ml">
						<tui-button type="black" :plain="true" width="148rpx" height="56rpx" :size="26" shape="circle">评价晒单</tui-button>
					</view>
					<view class="tui-btn-ml">
						<tui-button type="danger" :plain="true" width="148rpx" height="56rpx" :size="26" shape="circle" @click="order_again(order.order_id)">再次购买</tui-button>
					</view>
					<view class="tui-btn-ml" v-if="current_order_type == 2">
						<tui-button type="danger" :plain="true" width="148rpx" height="56rpx" :size="26" shape="circle" @click="confirm_take(order.order_id)">确认收货</tui-button>
					</view>
					<view class="tui-btn-ml" v-if="order.status  == 0 || current_order_type  == 1">
						<tui-button type="danger" :plain="true" width="148rpx" height="56rpx" :size="26" shape="circle" @click="cancel_order(order.order_id)">取消订单</tui-button>
					</view>
				</view>
			</view>

		</view>
		<!--加载loadding-->
		<tui-loadmore :visible="loadding" :index="3" type="red"></tui-loadmore>
		<tui-nomore :visible="!pullUpOn" bgcolor="#fafafa"></tui-nomore>
		<!--加载loadding-->

	</view>
</template>

<script>
	import tuiTabs from "@/components/tui-tabs/tui-tabs"
	import tuiButton from "@/components/extend/button/button"
	import tuiLoadmore from "@/components/loadmore/loadmore"
	import tuiNomore from "@/components/nomore/nomore"
	import tuiListCell from "@/components/list-cell/list-cell"
	import tuiSticky from "@/components/sticky/sticky"
	export default {
		components: {
			tuiTabs,
			tuiButton,
			tuiLoadmore,
			tuiNomore,
			tuiListCell,
			tuiSticky
		},
		data() {
			return {
				img_url:this.$img_url,
				tabs: [{
					name: "全部"
				}, {
					name: "待付款"
				}, {
					name: "待发货"
				}, {
					name: "待收货"
				}, {
					name: "待评价"
				}],
				currentTab: 0,
				current_order_type:999,
				pageIndex: 1,
				loadding: false,
				pullUpOn: true,
				scrollTop: 0,
				order_list:[],//订单列表
				page_limit:3,
			}
		},
		onLoad(option) {
			var type = option.state
			this.current_order_type = parseInt(type)
			if (this.current_order_type == 999){
				this.currentTab = 0
			}else{
				this.currentTab = this.current_order_type + 1
			}
			this.get_order_data()
		},
		methods: {
			change(e) {
				this.currentTab = e.index
				this.pageIndex = 1
				if(e.index == 0){
					this.current_order_type = 999
				}else{
				    this.current_order_type=(e.index-1)
				}
				this.order_list=[]
				this.pullUpOn = true
				this.loadding = false
				this.get_order_data()
			},
			detail(order_id) {
				uni.navigateTo({
					url: '/pages/user/myorder/myorder?id='+order_id
				})
			},
			get_order_data(){
				if(this.pullUpOn == false){
					return
				}
				var data = {type:this.current_order_type, limit:this.page_limit, page:this.pageIndex}
				this.loadding = true
				this.$api.CC_request.order_get_list(data).then(res => {
				    var data_list = res
					if(res.length > 0){
						this.order_list = this.order_list.concat(data_list)
					}
					
					//数据已加载完
					if(data_list.length < this.page_limit){
						this.loadding = false
						this.pullUpOn = false
					}
					
					this.loadding = false;
				})
			},
			order_again(order_id){
				this.$api.CC_request.order_again(order_id).then(res => {
					uni.setStorageSync('order_cart_id', res.cartId)
					uni.redirectTo({
						url: '/pages/order/createOrder?state=buy'
					})
				})
			},
			//确认收货
			confirm_take(order_id){
				this.$api.CC_request.order_confirm_take(order_id).then(res => {
					this.order_list=[]
					this.pullUpOn = true
					this.pageIndex = 1
					console.log("确认收货")
					this.get_order_data()
				})
			},
			//取消订单
			cancel_order(order_id){
				this.$api.CC_request.order_cancel(order_id).then(res => {
					this.order_list=[]
					this.pullUpOn = true
					this.pageIndex = 1
					this.get_order_data()
					this.$api.msg("取消订单成功")
				})
			}
		},
		onPullDownRefresh() {
			setTimeout(() => {
				uni.stopPullDownRefresh()
			}, 200);
		},
		onReachBottom() {
			if(this.pullUpOn == false){
			    return
			}
			this.pageIndex = this.pageIndex + 1
			this.get_order_data()
		},
		onPageScroll(e) {
			this.scrollTop = e.scrollTop;
		}
	}
</script>

<style>
	.container {
		padding-bottom: env(safe-area-inset-bottom);
	}

	.tui-order-list {
		margin-top: 80rpx;
	}

	.tui-order-item {
		margin-top: 20rpx;
		border-radius: 10rpx;
		overflow: hidden;
	}

	.tui-goods-title {
		width: 100%;
		font-size: 28rpx;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.tui-order-status {
		color: #888;
		font-size: 26rpx;
	}

	.tui-goods-item {
		width: 100%;
		padding: 20rpx 30rpx;
		box-sizing: border-box;
		display: flex;
		justify-content: space-between;
	}

	.tui-goods-img {
		width: 180rpx;
		height: 180rpx;
		display: block;
		flex-shrink: 0;
	}

	.tui-goods-center {
		flex: 1;
		padding: 20rpx 8rpx;
		box-sizing: border-box;
	}

	.tui-goods-name {
		max-width: 310rpx;
		word-break: break-all;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		font-size: 26rpx;
		line-height: 32rpx;
	}

	.tui-goods-attr {
		font-size: 22rpx;
		color: #888888;
		line-height: 32rpx;
		padding-top: 20rpx;
		word-break: break-all;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}

	.tui-price-right {
		text-align: right;
		font-size: 24rpx;
		color: #888888;
		line-height: 30rpx;
		padding-top: 20rpx;
	}

	.tui-color-red {
		color: #E41F19;
		padding-right: 30rpx;
	}

	.tui-goods-price {
		width: 100%;
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
		font-size: 24rpx;
	}

	.tui-size-24 {
		font-size: 24rpx;
		line-height: 24rpx;
	}

	.tui-price-large {
		font-size: 32rpx;
		line-height: 30rpx;
		font-weight: 500;
	}

	.tui-order-btn {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		background: #fff;
		padding: 10rpx 30rpx 20rpx;
		box-sizing: border-box;
	}

	.tui-btn-ml {
		margin-left: 20rpx;
	}
</style>
